<template>
  <v-container>
    <h1 class="mb-4">Ë®≠ÂÆöÁîªÈù¢</h1>

    <!-- „Éï„Ç°„Ç§„É´„Ç¢„ÉÉ„Éó„É≠„Éº„ÉâÈÉ®ÂàÜ -->
    <v-file-input v-model="file" label="CSV„Éï„Ç°„Ç§„É´„Çí„Ç¢„ÉÉ„Éó„É≠„Éº„Éâ" accept=".csv" @change="handleFileUpload"
      class="mb-4"></v-file-input>

    <!-- „Éï„Ç°„Ç§„É´„ÅÆÂÜÖÂÆπÁ¢∫Ë™ç„ÉªÁ∑®ÈõÜÈÉ®ÂàÜ -->
    <v-card v-if="sentences.length > 0" class="mb-4">
      <v-card-title>
        <v-text-field v-model="fileName" label="ÂçòÂÖÉÂêç„ÇíÂÖ•Âäõ" placeholder="‰æã: Unit1-Daily Life" class="mb-2"
          density="compact"></v-text-field>
      </v-card-title>

      <v-card-text>
        <div class="sentences-list">
          <div v-for="(sentence, index) in sentences" :key="index" class="sentence-item mb-4">
            <div class="d-flex align-center">
              <span class="mr-2">ÂïèÈ°å {{ index + 1 }}:</span>
              <v-text-field v-model="sentences[index]" density="compact" hide-details
                class="flex-grow-1"></v-text-field>
              <v-btn icon variant="text" color="error" size="large" class="ml-2" @click="removeSentence(index)">
                <v-icon>mdi-delete</v-icon>
              </v-btn>
            </div>

            <!-- „Éó„É¨„Éì„É•„ÉºË°®Á§∫ -->
            <div class="preview-chips ml-8 mt-2">
              <v-chip v-for="(word, wordIndex) in sentence.split(',')" :key="wordIndex" size="small" class="ma-1"
                :style="getChipStyle(word.trim(), index)" variant="tonal" @click="changeChipColor(word.trim(), index)">
                {{ word.trim() }}
              </v-chip>
            </div>
          </div>
        </div>
      </v-card-text>
      <v-card-actions>
        <v-btn prepend-icon="mdi-plus" variant="tonal" @click="addNewSentence" class="mr-2">
          Êñ∞„Åó„ÅÑÂïèÈ°å„ÇíËøΩÂä†
        </v-btn>

        <v-btn prepend-icon="mdi-content-cut" variant="tonal" @click="extractSVOC" class="mr-2" :loading="isProcessing"
          :disabled="isProcessing || !isValidData">
          ÂìÅË©ûÂàÜËß£
        </v-btn>

        <v-spacer></v-spacer>
        <v-btn prepend-icon="mdi-check" variant="tonal" color="primary" @click="saveSentences" :disabled="!isValidData">
          ‰øùÂ≠ò
        </v-btn>
      </v-card-actions>
    </v-card>

    <!-- „É°„ÉÉ„Çª„Éº„Ç∏Ë°®Á§∫ -->
    <v-alert v-if="message" :type="messageType" class="mt-4" closable>
      {{ message }}
    </v-alert>
  </v-container>
</template>

<script>
import { db } from '../firebase/init'
import { collection, addDoc, query, getDocs, where, updateDoc } from 'firebase/firestore'
import axios from 'axios'

export default {
  name: 'SettingsScreen',
  data() {
    return {
      file: null,
      sentences: [], // ÂÖÉ„ÅÆÊñáÁ´†Ôºà„Ç´„É≥„ÉûÂå∫Âàá„ÇäÔºâ
      analyzedSentences: [], // Ëß£ÊûêÁµêÊûú„Çí‰øùÊåÅ
      wordColors: [], // ÂêÑÂçòË™û„ÅÆËâ≤ÊÉÖÂ†±„Çí‰øùÊåÅ
      message: '',
      messageType: 'info',
      fileName: '',
      isProcessing: false,
      colorScheme: {
        subject: '#FFEB3B', // ÈªÑËâ≤
        verb: '#A5D6A7', // Á∑ë
        object: '#90CAF9', // Èùí
        complement: '#FF9800', // „Ç™„É¨„É≥„Ç∏
        other: '#E0E0E0' // „Åù„ÅÆ‰ªñÔºà„Ç∞„É¨„ÉºÔºâ
      },
      apiConfig: {
        baseURL: 'https://english-app-serverside.onrender.com',
        timeout: 500000000,
        headers: {
          'Content-Type': 'application/json',
          // CORS„Éó„É™„Éï„É©„Ç§„Éà„É™„ÇØ„Ç®„Çπ„Éà„ÅÆ„Åü„ÇÅ„ÅÆËøΩÂä†„Éò„ÉÉ„ÉÄ„Éº
          'Accept': 'application/json'
        }
      }
    }
  },
  computed: {
    isValidData() {
      return this.fileName.trim() !== '' &&
        this.sentences.length > 0 &&
        this.sentences.every(s => s.trim() !== '')
    }
  },
  methods: {
    getWordType(word, analysis) {
      if (analysis.subject.includes(word)) return 'subject'
      if (analysis.verb.includes(word)) return 'verb'
      if (analysis.object.includes(word)) return 'object'
      if (analysis.complement.includes(word)) return 'complement'
      return 'other'
    },
    getWordColor(word, index) {
      return this.wordColors[index]?.[word] || this.colorScheme.other
    },

    getChipStyle(word, sentenceIndex) {
      const backgroundColor = this.wordColors[sentenceIndex]?.[word] || this.colorScheme.other;
      // „Éê„ÉÉ„ÇØ„Ç∞„É©„Ç¶„É≥„Éâ„ÇíËñÑ„Åè„ÄÅ„ÉÜ„Ç≠„Çπ„Éà„ÇíÊøÉ„Åè
      return {
        backgroundColor: backgroundColor,
        color: this.getContrastColor(backgroundColor),
        borderColor: backgroundColor,
      }
    },

    changeChipColor(word, sentenceIndex) {
      // ÂÆâÂÖ®„Å´ÂàùÊúüÂåñ
      if (!this.wordColors[sentenceIndex]) {
        this.wordColors[sentenceIndex] = {};
      }

      // ÁèæÂú®„ÅÆËâ≤„ÇíÂèñÂæóÔºàË¶ã„Å§„Åã„Çâ„Å™„ÅÑÂ†¥Âêà„ÅØ "other" „ÅÆËâ≤„Çí‰Ωø„ÅÜÔºâ
      const currentValue = this.wordColors[sentenceIndex]?.[word] || this.colorScheme.other;

      // ÁèæÂú®„ÅÆËâ≤„Å´ÂØæÂøú„Åô„Çã„Ç≠„Éº„Çí„Åô„Åπ„Å¶ÂèñÂæóÔºàÈáçË§áËâ≤„Å´„ÇÇÂØæÂøúÔºâ
      const matchingKeys = Object.keys(this.colorScheme).filter(
        (key) => this.colorScheme[key] === currentValue
      );

      // Ë§áÊï∞„ÅÆ„Ç≠„Éº„ÅåË¶ã„Å§„Åã„Å£„ÅüÂ†¥Âêà„ÅØ„ÄÅ„Åù„ÅÆ‰∏≠„ÅÆ1„Å§„ÇíÈÅ∏„Å∂ÔºàÂÖàÈ†≠„Çí‰ΩøÁî®Ôºâ
      const currentKey = matchingKeys[0];

      const keys = Object.keys(this.colorScheme); // ÂÖ®„Å¶„ÅÆ„Ç≠„Éº„ÇíÂèñÂæó
      const currentIndex = keys.indexOf(currentKey); // ÁèæÂú®„ÅÆ„Ç§„É≥„Éá„ÉÉ„ÇØ„Çπ„ÇíÂèñÂæó

      // Ê¨°„ÅÆ„Ç§„É≥„Éá„ÉÉ„ÇØ„Çπ„ÇíË®àÁÆóÔºàÂæ™Áí∞„Åô„Çã„Çà„ÅÜ„Å´Ôºâ
      const nextIndex = (currentIndex + 1) % keys.length;

      // Ê¨°„ÅÆËâ≤„ÇíË®≠ÂÆö
      this.wordColors[sentenceIndex][word] = this.colorScheme[keys[nextIndex]];

    },

    getContrastColor(hexColor) {
      // „Ç´„É©„Éº„Ç≥„Éº„Éâ„ÇíRGB„Å´Â§âÊèõ
      const r = parseInt(hexColor.slice(1, 3), 16);
      const g = parseInt(hexColor.slice(3, 5), 16);
      const b = parseInt(hexColor.slice(5, 7), 16);

      // ËºùÂ∫¶„ÇíË®àÁÆó
      const brightness = (r * 299 + g * 587 + b * 114) / 1000;

      // Êòé„Çã„ÅÑËÉåÊôØ„Å´„ÅØÊöó„ÅÑ„ÉÜ„Ç≠„Çπ„Éà„ÄÅÊöó„ÅÑËÉåÊôØ„Å´„ÅØÊòé„Çã„ÅÑ„ÉÜ„Ç≠„Çπ„Éà
      return brightness > 128 ? '#000000' : '#FFFFFF';
    },

    handleFileUpload() {
      if (this.file) {
        const reader = new FileReader()
        reader.onload = (e) => {
          const content = e.target.result
          this.sentences = content.split('\n')
            .filter(line => line.trim() !== '')
            .map(line => line.trim())

          // „Éï„Ç°„Ç§„É´Âêç„Åã„ÇâÊã°ÂºµÂ≠ê„ÇíÈô§„ÅÑ„Å¶ÂçòÂÖÉÂêç„ÅÆÂàùÊúüÂÄ§„Å®„Åó„Å¶Ë®≠ÂÆö
          if (this.file.name) {
            this.fileName = this.file.name.replace(/\.csv$/, '')
          }
        }
        reader.readAsText(this.file)
      }
    },

    addNewSentence() {
      this.sentences.push('')
    },

    removeSentence(index) {
      this.sentences.splice(index, 1)
    },

    validateSentence(sentence) {
      // „Ç´„É≥„ÉûÂå∫Âàá„Çä„ÅÆÂΩ¢Âºè„ÅåÊ≠£„Åó„ÅÑ„Åã„ÉÅ„Çß„ÉÉ„ÇØ
      const words = sentence.split(',')
      return words.length >= 2 && words.every(word => word.trim() !== '')
    },

    async extractSVOC() {
      if (!this.isValidData) {
        this.showMessage('ÂçòÂÖÉÂêç„Å®Â∞ë„Å™„Åè„Å®„ÇÇ1„Å§„ÅÆÊúâÂäπ„Å™ÂïèÈ°å„ÅåÂøÖË¶Å„Åß„Åô„ÄÇ', 'error')
        return
      }

      this.isProcessing = true
      this.showMessage('ÊñáÁ´†„ÇíËß£Êûê‰∏≠„Åß„Åô...', 'info')

      try {
        this.analyzedSentences = []
        this.wordColors = []

        const api = axios.create({
          baseURL: 'https://english-app-serverside.onrender.com',
          headers: {
            'Content-Type': 'application/json'
          },
          // render„ÅÆ„Çµ„Éº„Éê„Éº„ÅØÂøúÁ≠î„ÅåÈÅÖ„ÅÑ„Åü„ÇÅ„ÄÅ„Çø„Ç§„É†„Ç¢„Ç¶„Éà„ÇíÈï∑„ÇÅ„Å´Ë®≠ÂÆö
          timeout: 30000
        })

        for (let i = 0; i < this.sentences.length; i++) {
          const sentence = this.sentences[i]
          const words = sentence.split(',')
          const wholeSentence = words.join(' ').trim()

          if (!wholeSentence) continue

          try {
            const response = await api.post('/analyze', {
              text: wholeSentence
            })

            if (response.data) {
              this.analyzedSentences[i] = response.data
              const colors = {}
              words.forEach(word => {
                const cleanWord = word.trim()
                const wordType = this.getWordType(cleanWord, response.data)
                colors[cleanWord] = this.colorScheme[wordType]
              })
              this.wordColors[i] = colors
            }

          } catch (error) {
            if (axios.isAxiosError(error)) {
              if (error.code === 'ECONNABORTED') {
                throw new Error('„Çµ„Éº„Éê„Éº„ÅÆÂøúÁ≠î„Åå„Çø„Ç§„É†„Ç¢„Ç¶„Éà„Åó„Åæ„Åó„Åü„ÄÇ„Åó„Å∞„Çâ„ÅèÂæÖ„Å£„Å¶„Åã„ÇâÂÜçË©¶Ë°å„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ')
              }
              if (error.response) {
                throw new Error(`„Çµ„Éº„Éê„Éº„Ç®„É©„Éº: ${error.response.status} - ${error.response.data?.error || '‰∏çÊòé„Å™„Ç®„É©„Éº'}`)
              }
              if (error.request) {
                throw new Error('„Çµ„Éº„Éê„Éº„Å´Êé•Á∂ö„Åß„Åç„Åæ„Åõ„Çì„ÄÇ„Ç§„É≥„Çø„Éº„Éç„ÉÉ„ÉàÊé•Á∂ö„ÇíÁ¢∫Ë™ç„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ')
              }
            }
            throw error
          }
        }

        this.showMessage('Ëß£Êûê„ÅåÂÆå‰∫Ü„Åó„Åæ„Åó„Åü„ÄÇ', 'success')
      } catch (error) {
        this.showMessage(`Ëß£Êûê‰∏≠„Å´„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü: ${error.message}`, 'error')
      } finally {
        this.isProcessing = false
      }
    },

    async saveSentences() {
      if (!this.isValidData) {
        this.message = 'ÂçòÂÖÉÂêç„Å®Â∞ë„Å™„Åè„Å®„ÇÇ1„Å§„ÅÆÊúâÂäπ„Å™ÂïèÈ°å„ÅåÂøÖË¶Å„Åß„Åô„ÄÇ'
        this.messageType = 'error'
        return
      }

      this.isProcessing = true
      this.showMessage('ÊñáÁ´†„Çí‰øùÂ≠ò‰∏≠„Åß„Åô...', 'info')

      try {
        const lessonsRef = collection(db, 'lessons')
        const q = query(lessonsRef, where("name", "==", this.fileName))
        const querySnapshot = await getDocs(q)

        const lessonData = {
          name: this.fileName,
          sentences: this.sentences,
          analyzedSentences: this.analyzedSentences,
          wordColors: this.wordColors,
          createdAt: new Date().toISOString()
        }

        if (querySnapshot.empty) {
          await addDoc(lessonsRef, lessonData)
        } else {
          const docRef = querySnapshot.docs[0].ref
          await updateDoc(docRef, lessonData)
        }

        this.message = 'ÊñáÁ´†„ÅåÊ≠£Â∏∏„Å´‰øùÂ≠ò„Åï„Çå„Åæ„Åó„Åü„ÄÇ'
        this.messageType = 'success'
      } catch (error) {
        this.message = '‰øùÂ≠ò‰∏≠„Å´„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü„ÄÇ'
        this.messageType = 'error'
      }
    },
    showMessage(text, type = 'info') {
      this.message = text
      this.messageType = type
    }
  }
}
</script>

<style scoped>
.sentences-list {
  max-height: 60vh;
  overflow-y: auto;
}

.sentence-item {
  border-bottom: 1px solid #e0e0e0;
  padding-bottom: 8px;
}

.sentence-item:last-child {
  border-bottom: none;
}

.preview-chips {
  display: flex;
  flex-wrap: wrap;
  gap: 4px;
}
</style>
